.l-header {
    position: fixed;
    top: 0;
    @include size(100%, 70px);
    background-color: transparent;
    z-index: 99;
    transition: .3s all ease-in-out;

    &__wrapper {
        max-width: 1170px;
        height: 100%;
        margin: 0 auto;
        @include flex(flex, center, space-between);

        padding: 0 30px;

    }

    &__sticky {
        background-color: #fff;
        // opacity: .9;
    }
}